import styled from "styled-components";

const FooterStyled = styled.footer`
   width: 100%;
   background: linear-gradient(to bottom, var(--yellow), var(--orange));
   padding: 10px;
   text-align: center;
   color: #fff;
`;

export { FooterStyled };



